.logo {
  display: block;
  padding: 0 20px 12px;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    border-bottom: none;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    padding: 0;
  }
}

.logo__img {
  width: 191px;
  height: 38px;

  &:hover,
  &:focus {
    opacity: 0.8;
    transition: 0.3s;
  }

  &:active {
    opacity: 0.6;
    transition: 0.3s;
  }

  @media (min-width: $tablet-width) {
    width: 174px;
    height: 59px;
  }

  @media (min-width: $desktop-width) {
    width: 203px;
    height: 60px;
  }
}
